import React from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';

import SVG from './SVG';

import monstera from '../SVG/logo';

const Brand = (props) => {
  return (
    <NavLink className={props.className} to="/">
      <SVG
        viewBox={monstera.viewBox}
        height={monstera.height}
        paths={monstera.paths}
        id={monstera.id}
        alt="logo"
      />
      <h3>
        <span className="sr-only">Home</span>
        <span id="regex" aria-hidden={true}>&nbsp;Gab+[iy]e?&nbsp;</span>{
          // eslint-disable-next-line
        }<span id="gabbie" aria-hidden={true}>/* Gabbie */</span>
      </h3>
    </NavLink>
  );
};

const StyledBrand = styled(Brand)`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: .75rem;

  #monstera {
    height: 1.5rem;
    padding-right: 1rem;

    @media (min-width: 768px) {
      height: 1.75rem;
    }
  }

  #gabbie {
    display: none;
  }

  &:hover #regex,
  &:focus #regex {
    display: none;
  }

  &:hover #gabbie,
  &:focus #gabbie {
    display: unset;
  }
`;

export default StyledBrand;